import { Component, Element, Event, EventEmitter, h, Host, Method, Prop } from '@stencil/core';
import { SharedStyles } from '@utils/shared-styles';
import Swal from 'sweetalert2';

@Component({
  tag: 'storm-modal',
  styleUrl: 'storm-modal.css',
  shadow: true,
})
export class StormModal {
  @Element() host: HTMLElement;
  @Prop() width: '32em';
  @Event() close: EventEmitter<boolean>;

  templateRef: HTMLTemplateElement;
  slotRef: any;

  @Method()
  async fire(callback: Function) {
    await Swal.fire({
      width: this.width,
      allowEnterKey: false,
      template: this.templateRef,
    }).then(result => {
      this.close.emit(result.isConfirmed);
      if (callback) callback(result.isConfirmed);
    });
  }

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');
  }

  componentDidLoad() {
    this.slotRef.assignedElements().map(el => {
      this.templateRef.content.appendChild(el);
    });
  }

  render() {
    return (
      <Host>
        <template ref={el => (this.templateRef = el)}>
          <slot ref={el => (this.slotRef = el)} />
        </template>
      </Host>
    );
  }
}
